import { NgModule } from '@angular/core'
import { RouterModule } from '@angular/router'
import { TranslateModule } from '@ngx-translate/core'

import {
    MatButtonModule,
    MatDialogModule,
    MatDividerModule,
    MatIconModule,
    MatInputModule,
    MatListModule,
    MatMenuModule,
    MatProgressBarModule,
    MatSelectModule,
    MatTooltipModule
} from '@angular/material'

import { FuseSharedModule } from '@fuse/shared.module'

import { NgxPaginationModule } from 'ngx-pagination'
import { OrderModule } from 'ngx-order-pipe'

import { AuthGuard } from 'app/guard/guard-auth.guard'

import { ListRoleComponent } from './list-role/list-role.component'
import { DialogAddRole } from './list-role/dialog/dialog-add-role/dialog-add-role'
import { DialogEditRole } from './list-role/dialog/dialog-edit-role/dialog-edit-role'

const routes = [
    {
        path: '',
        component: ListRoleComponent,
        canActivate: [AuthGuard]
    }
]

@NgModule({
    declarations: [
        ListRoleComponent,
        DialogAddRole,
        DialogEditRole
    ],
    entryComponents: [
        DialogAddRole,
        DialogEditRole
    ],
    imports: [
        RouterModule.forChild(routes),

        TranslateModule,

        MatButtonModule,
        MatDialogModule,
        MatDividerModule,
        MatIconModule,
        MatInputModule,
        MatListModule,
        MatMenuModule,
        MatProgressBarModule,
        MatSelectModule,
        MatTooltipModule,

        FuseSharedModule,

        NgxPaginationModule,
        OrderModule
    ]
})

export class RoleModule { }