import { Component, ViewEncapsulation, OnInit } from '@angular/core'
import { fuseAnimations } from '@fuse/animations'
import { Router } from '@angular/router'
import { SubscriptionLike } from 'rxjs'

import { MatDialog } from '@angular/material'

import { UtilityService } from 'app/services/utility.service'

import { DialogAddRole } from './dialog/dialog-add-role/dialog-add-role'

import { DialogEditRole } from './dialog/dialog-edit-role/dialog-edit-role'

@Component({
    selector: 'list-role',
    templateUrl: './list-role.component.html',
    styleUrls: ['./list-role.component.scss'],
    providers: [
        UtilityService
    ],
    encapsulation: ViewEncapsulation.None,
    animations: fuseAnimations
})
export class ListRoleComponent implements OnInit {
    isLoading: boolean
    isSuccessFetch: boolean
    reverseListRole: boolean

    orderListRole: string
    itemListRole: string
    failMessage: string

    pageListRole: number

    filteredListRole = []
    arrListRole = []

    timeout: NodeJS.Timer
    subLoadListRole: SubscriptionLike

    constructor(
        private router: Router,
        private dialog: MatDialog,

        private utilityService: UtilityService
    ) {
        this.orderListRole = "role_id"
        this.itemListRole = "10"
        this.pageListRole = 1
    }

    ngOnInit(): void {
        this.isLoading = false
        this.isSuccessFetch = true
        this.filteredListRole = listRole
        this.arrListRole = listRole
    }

    ngOnDestroy(): void {
        clearTimeout(this.timeout)

        if (this.subLoadListRole) {
            this.subLoadListRole.unsubscribe()
        }
    }

    timeoutCondition(hitApi: any) {
        hitApi.unsubscribe()
        clearTimeout(this.timeout)

        this.isLoading = false
        this.isSuccessFetch = false

        this.failMessage = "Timeout, Mohon periksa koneksi internet anda"
    }

    filterListRole(filterValue: string) {
        filterValue = filterValue.trim()
        filterValue = filterValue.toLowerCase()

        this.arrListRole = this.filteredListRole.filter(item => item.role_name.includes(filterValue))
    }

    setOrderListRole(value: string) {
        if (this.orderListRole === value) {
            this.reverseListRole = !this.reverseListRole;
        }

        this.orderListRole = value
    }

    addRoleDialog() {
        this.dialog.open(DialogAddRole, {
            width: "500px",
            disableClose: true
        })
    }

    editRoleDialog(ListRole: any) {
        this.dialog.open(DialogEditRole, {
            width: "500px",
            disableClose: true,
            data: {
                ListRole: ListRole
            }
        })
    }
}

const listRole = [
    {
        role_id: 1,
        role_name: "administrator",
        menu_detail: [
            {
                menu_id: 1,
                menu_name: "Dashboard"
            },
            {
                menu_id: 2,
                menu_name: "Data User"
            },
            {
                menu_id: 3,
                menu_name: "Data Role"
            }
        ]
    },
    {
        role_id: 2,
        role_name: "vendor admin",
        menu_detail: [
            {
                menu_id: 1,
                menu_name: "Dashboard"
            },
            {
                menu_id: 4,
                menu_name: "Data Customer"
            },
            {
                menu_id: 5,
                menu_name: "Data Truck"
            }
        ]
    },
    {
        role_id: 3,
        role_name: "operasional",
        menu_detail: [
            {
                menu_id: 1,
                menu_name: "Dashboard"
            },
            {
                menu_id: 6,
                menu_name: "Data Vendor"
            }
        ]
    }
]