import { Component, Inject, OnInit } from "@angular/core";
import { MAT_DIALOG_DATA } from "@angular/material";

@Component({
    selector: 'dialog-edit-role',
    templateUrl: 'dialog-edit-role.html'
})

export class DialogEditRole implements OnInit {
    listMenu = []
    listSelectedMenu = []

    constructor(
        @Inject(MAT_DIALOG_DATA)
        public data: any
    ) {

    }

    ngOnInit(): void {
        this.listMenu = menu_list
        this.listSelectedMenu = this.data.ListRole.menu_detail

        for (let i = 0; i < this.listMenu.length; i++) {
            for (let j = 0; j < this.listSelectedMenu.length; j++) {
                if (this.listMenu[i].menu_id == this.listSelectedMenu[j].menu_id) {
                    this.listMenu[i].status = true
                    break;
                }
            }
        }
    }
}

const menu_list = [
    {
        menu_id: 1,
        menu_name: "Dashboard",
        status: false
    },
    {
        menu_id: 2,
        menu_name: "Data User",
        status: false
    },
    {
        menu_id: 3,
        menu_name: "Data Role",
        status: false
    },
    {
        menu_id: 4,
        menu_name: "Data Customer",
        status: false
    },
    {
        menu_id: 5,
        menu_name: "Data Truck",
        status: false
    },
    {
        menu_id: 6,
        menu_name: "Data Vendor",
        status: false
    }
]